class TextScrambler {
    constructor(el) {
        this.el = el
        this.chars = '!<>-_\\/[]{}â€”=+*^?#________'
        this.update = this.update.bind(this)
    }

    setText(newText) {
        const oldText = this.el.innerText
        const length = Math.max(oldText.length, newText.length)
        const promise = new Promise((resolve) => this.resolve = resolve)
        this.queue = []
        for (let i = 0; i < length; i++) {
            const from = oldText[i] || ''
            const to = newText[i] || ''
            const start = Math.floor(Math.random() * 40)
            const end = start + Math.floor(Math.random() * 40)
            this.queue.push({ from, to, start, end })
        }
        cancelAnimationFrame(this.frameRequest)
        this.frame = 0
        this.update()
        return promise
    }

    update() {
      let output = ''
      let complete = 0
      for (let i = 0, n = this.queue.length; i < n; i++) {
        let { from, to, start, end, char } = this.queue[i]
        if (this.frame >= end) {
            complete++
            output += to
        } else if (this.frame >= start) {
            if (!char || Math.random() < 0.28) {
                char = this.randomChar()
                this.queue[i].char = char
            }
            output += `<span class="dud">${char}</span>`
        } else {
            output += from
        }
      }
      this.el.innerHTML = output
      if (complete === this.queue.length) {
            this.resolve()
      } else {
            this.frameRequest = requestAnimationFrame(this.update)
            this.frame++
      }
    }

    randomChar() {
        return this.chars[Math.floor(Math.random() * this.chars.length)]
    }
}

const throttle = (func, limit) => {
    let lastFunc;
    let lastRan;
    return function() {
        const context = this;
        const args = arguments;
        if (!lastRan) {
            func.apply(context, args);
            lastRan = Date.now();
        } else {
            clearTimeout(lastFunc);
            lastFunc = setTimeout(function() {
                if ((Date.now() - lastRan) >= limit) {
                    func.apply(context, args);
                    lastRan = Date.now();
                }
            }, limit - (Date.now() - lastRan));
        }
    }
}

class Nav {
    constructor() {
        const navHeight = document.querySelector(".nav").getBoundingClientRect().height;
        const mainNavLinks = document.querySelectorAll(".nav .sections a");

        window.addEventListener("scroll", throttle(() => {

                const fromTop = window.scrollY;

                let prevSectionEnd = document.querySelector(mainNavLinks[0].hash).offsetTop - navHeight
                for (let i = 0; i < mainNavLinks.length; i++) {
                    const link = mainNavLinks[i];
                    
                    const section = document.querySelector(link.hash);
                    const sectionEnd = section.offsetTop - navHeight + section.offsetHeight

                    if (
                        prevSectionEnd <= fromTop &&
                        sectionEnd > fromTop
                    ) {
                        link.classList.add("active");
                    } else {
                        link.classList.remove("active");
                    }

                    prevSectionEnd = sectionEnd
                }
            }, 100));
    }
}

(function() {
    const phrases = [
        "I'm a Software Engineer.",
        "I love solving problems ",
        "and learning new things!",
    ]

    const el = document.querySelector('.text')
    const fx = new TextScrambler(el)

    let counter = 0
    const next = () => {
        fx.setText(phrases[counter]).then(() => {
            setTimeout(next, 800)
        })
        counter = (counter + 1) % phrases.length
    }
    
    next()

    new Nav()
})();

(function() {
    const contactForm = document.getElementById('contact-form')

    let disabled = false

    contactForm.addEventListener('submit', e => {
        e.preventDefault()

        if (disabled) {
            return
        }

        disabled = true
        contactForm.classList.add('disabled')

        contactForm.querySelector('button[type="submit"]').innerText = "Sending..."

        fetch('/contact', {
            method: 'POST',
            body: JSON.stringify({
                name: contactForm.querySelector('input[name="name"]').value,
                email: contactForm.querySelector('input[name="email"]').value,
                message: contactForm.querySelector('textarea[name="message"]').value,
            })
        })
        .then((response) => {
            if (response.ok) {
                contactForm.querySelector('button[type="submit"]').innerText = "Sent"
            } else {
                disabled = false
                contactForm.classList.remove('disabled')
                contactForm.querySelector('button[type="submit"]').innerText = "Failed"
                setTimeout(() => {
                    contactForm.querySelector('button[type="submit"]').innerText = "Send Message"
                }, 1000)
            }
        })
    })
})();